<template>
    <el-dialog v-dialogDrag :title="dialogTitle" :visible.sync="visible" @close="closeDialog(false)">
        <el-form size="mini" :inline="true">
            <el-form-item label="">
                <el-input v-model="dialogData.code" placeholder="任务编码" :maxlength="20" class="inputwinth192">
                    <template slot="prepend">任务编码</template>
                </el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="dialogData.jobGroup" placeholder="任务分组" :maxlength="20" class="inputwinth192">
                    <template slot="prepend">任务分组</template>
                </el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="dialogData.name" placeholder="任务名称" :maxlength="20" class="inputwinth192">
                    <template slot="prepend">任务名称</template>
                </el-input>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="dialogData.remarks" placeholder="路由地址" :maxlength="120" class="inputwinth397">
                    <template slot="prepend">路由地址</template>
                </el-input>
            </el-form-item>
            <el-form-item label="时间规则" label-width="70px">
                <el-select v-model="dialogData.jobCron" placeholder="请选择" :maxlength="20" class="inputwinth122">
                    <el-option v-for="item in global.jobCronList" :key="item.id" :label="item.name" :value="item.code">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label="任务状态" label-width="80px">
                <el-radio v-model="dialogData.jobStatus" :label="1">启动</el-radio>
                <el-radio v-model="dialogData.jobStatus" :label="0">停止</el-radio>
            </el-form-item>
            <el-form-item label="">
                <el-input v-model="dialogData.sortNum" placeholder="排序" :maxlength="20" class="inputwinth192">
                    <template slot="prepend">排序</template>
                </el-input>
            </el-form-item>
            <el-form-item label="状态" label-width="80px">
                <el-radio v-model="dialogData.is_Enable" :label="1">启用</el-radio>
                <el-radio v-model="dialogData.is_Enable" :label="0">弃用</el-radio>
            </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button size="mini" icon="fa fa-file-o" @click="closeDialog(false)"> 取 消</el-button>
            <el-button size="mini" icon="fa fa-file-text-o" type="primary" @click="saveDialog"> 保 存</el-button>
        </div>
    </el-dialog>
</template>
 
<script>
export default {
    data() {
        return {
            moduleName: "quartz",
            visible: this.dialogShow
        };
    },
    props: {
        dialogTitle: "",
        dialogShow: false,
        dialogData: {
            id: 0,
            code: "",
            name: "",
            is_Enable: 1,
            is_Hide: 0
        }
    },
    methods: {
        closeDialog(isclose) {
            this.$emit("update:dialogShow", isclose);
        },
        saveDialog() {
            var url =
                this.moduleName +
                (this.dialogData.id > 0 ? "/updateOne" : "/insertOne");
            this.$ajax.post(url, this.dialogData).then(res => {
                this.$emit("update:dialogData", res.data);
                this.$emit("update:dialogShow", false);
                this.$emit("RefreshData");
            });
        }
    },
    watch: {
        dialogShow() {
            this.visible = this.dialogShow;
        }
    }
};
</script>



